.welcome {
  width: 100vw;
  min-width: $vp-1024;
  background-color: #1f2227;
  background-image: linear-gradient(rgba(0, 0, 0, 0.6)), url("../img/welcome-background.jpg");
  background-position: center;
  background-size: 100% 100%;
  background-repeat: no-repeat;


  @media (min-width: $vp-1024) {
    @media (min-resolution: 2dppx) {
      background-image: linear-gradient(rgba(0, 0, 0, 0.6)), url("../img/welcome-background@2x.jpg");
    }
  }

  @media (max-width: $vp-1023) {
    min-width: 768px;
    background-image: url("../img/welcome-background.jpg");

    @media (min-resolution: 2dppx) {
      background-image: url("../img/welcome-background@2x.jpg");
    }
  }

  @media (max-width: $vp-767) {
    min-width: 320px;
    background-image: url("../img/welcome-background.jpg");

    @media (min-resolution: 2dppx) {
      background-image: url("../img/welcome-background@2x.jpg");
    }
  }
}

.welcome > div {
  width: 100%;
  margin: 160px auto 0 auto;
  max-width: 1920px;
  padding: 250px 15% 278px 10%;
  z-index: 3;

  @media (max-width: $vp-1023) {
    padding: 180px 6.5% 204px 6.5%;
  }

  @media (max-width: $vp-767) {
    padding: 150px 8.5% 190px 8.5%;
  }
}

.welcome h2 {
  color: $color-default-white;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 80px;
  line-height: 94px;
  font-weight: 700;
  margin: 0 0 40px 0;

  @media (max-width: $vp-1023) {
    font-size: 64px;
    line-height: 75px;
    margin: 0;
  }

  @media (max-width: $vp-767) {
    font-size: 36px;
    line-height: 42px;
    margin: 0 0 8px 0;
  }
}

.welcome p {
  color: $color-default-white;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 30px;
  line-height: 34px;
  font-weight: 400;
  text-transform: lowercase;
  margin: 0 0 50px 0;
  max-width: 680px;

  @media (max-width: $vp-1023) {
    margin: 0 0 58px 0;
  }

  @media (max-width: $vp-767) {
    font-size: 14px;
    line-height: 22px;
    margin: 0 0 45px 0;
  }
}

.welcome a {
  display: inline-block;
  vertical-align: baseline;
  color: $color-default-white;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 18px;
  line-height: 21px;
  font-weight: 700;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  padding: 16px 26px 18px 26px;
  background-color: #ff9100;
  border-radius: 3px;
  user-select: none;

  @media (max-width: $vp-1023) {
    margin: 0 0 0 45px;
    padding: 17px 27px;
  }

  @media (max-width: $vp-767) {
    font-weight: 700;
    font-size: 14px;
    line-height: 16px;
    padding: 12px 35px;
    margin: 0;
  }

  &:hover {
    outline: 2px solid #00b2ff;
    outline-offset: 2px;
  }

  &:active {
    outline: 2px solid #00b2ff;
    outline-offset: 1px;
  }
}
